"use server";

import {
  createAdmin,
  createServerClientInstance,
} from "@/utils/supabase/server";
import { revalidatePath } from "next/cache";
import { redirect } from "next/navigation";

//REGISTER

export async function createMember(data: {
  email: string;
  password: string;
  role: "editor" | "admin";
  name: string;
}) {
  const supabase = await createAdmin();

  try {
    const createResult = await supabase.auth.admin.createUser({
      email: data.email,
      password: data.password,
      email_confirm: true,
      user_metadata: {
        role: data.role,
      },
    });

    if (createResult.error) {
      console.error("Failed to create user:", createResult.error.message);
      throw new Error("Failed to create user: " + createResult.error.message);
    }

    console.log("User created:", createResult.data.user);

    const memberResult = await supabase
      .from("members")
      .insert({ name: data.name, id: createResult.data.user?.id });

    if (memberResult.error) {
      console.error(
        "Failed to insert into members:",
        memberResult.error.message
      );
      throw new Error(
        "Failed to insert into members: " + memberResult.error.message
      );
    }

    console.log("Member inserted:", memberResult.data);

    const permissionsResult = await supabase
      .from("permissions")
      .insert({ role: data.role, member_id: createResult.data.user?.id });

    if (permissionsResult.error) {
      console.error(
        "Failed to insert into permissions:",
        permissionsResult.error.message
      );
      throw new Error(
        "Failed to insert into permissions: " + permissionsResult.error.message
      );
    }

    console.log("Permissions inserted:", permissionsResult.data);

    return createResult.data.user;
  } catch (err) {
    console.error("Unexpected error during member creation:", err);
    throw err;
  }
}

//LOGOUT

export async function signOut() {
  const supabase = await createServerClientInstance();

  await supabase.auth.signOut();

  revalidatePath("/", "layout");
  redirect("/login");
}

// ADMIN GET ALL USERS

export async function getAllUsers() {
  const supabase = await createAdmin();

  const {
    data: { users },
    error: fetchError,
  } = await supabase.auth.admin.listUsers();

  if (fetchError) {
    throw new Error("Failed to fetch users: " + fetchError.message);
  }

  return users || [];
}

export async function readUserSession() {
  const supabase = await createServerClientInstance();

  // Trin 1: Hent brugerdata
  const { data: userResponse, error: userError } =
    await supabase.auth.getUser();

  if (userError || !userResponse?.user) {
    console.error(
      "User fetch error:",
      userError?.message || "Ingen bruger fundet"
    );
    return null;
  }

  const userId = userResponse.user.id;

  // Trin 2: Hent medlemmet baseret pÃ¥ id
  const { data: member, error: memberError } = await supabase
    .from("members")
    .select("id, name")
    .eq("id", userId) // Brug userId som id
    .single();

  if (memberError || !member) {
    console.error(
      "Fejl ved hentning af medlem:",
      memberError?.message || "Ingen medlem fundet"
    );
    return null;
  }

  // Trin 3: Hent rollen fra permissions-tabellen
  const { data: permission, error: permissionError } = await supabase
    .from("permissions")
    .select("role")
    .eq("member_id", member.id) // Brug member.id
    .single();

  if (permissionError || !permission) {
    console.error(
      "Fejl ved hentning af rolle:",
      permissionError?.message || "Ingen rolle fundet"
    );
    return null;
  }

  return {
    user: userResponse.user,
    member,
    role: permission.role,
  };
}
